const fs = window.require('fs');

const errors = {
    invalidQR: "Ungültiger QR-Code",
    invalidResponse: "Ubgültige Antwort vom Kassenserver",
    defaultError: 'Unbekannter Fehler',
    'This token already used': 'Dieses Token wurde bereits verwendet',
    'Your token time is expire': 'Ihre Tokenzeit ist abgelaufen'
};

function getError(error) {
    if (errors.hasOwnProperty(error.message)) {
        return errors[error.message];
    }
    if (errors.hasOwnProperty(error)) {
        return errors[error];
    }
    return `${error.whoIsGuilty} ${errors.defaultError}`;
}

export default getError;
