import React, {Component} from 'react';

class WebCamera extends Component {

    componentDidMount() {
        this.props.setRef(this);
        this.startVideo();
    }

    takeFoto() {
        let canvasElement = document.createElement("canvas");

        let canvas = canvasElement.getContext("2d");
        if (this.video && this.video.readyState === this.video.HAVE_ENOUGH_DATA) {
            canvasElement.hidden = false;
            canvasElement.height = this.video.videoHeight;
            canvasElement.width = this.video.videoWidth;

            canvas.drawImage(this.video, 0, 0, canvasElement.width, canvasElement.height);
            // let imageData = canvas.getImageData(0, 0, canvasElement.width, canvasElement.height);
            const result = canvasElement.toDataURL(
                this.props.imageType || 'image/jpeg',
                this.props.imageQuality || 1
            );

            canvasElement.remove();
            return result;
        }
    }

    async startVideo() {
        this.video = document.getElementById("video");
        const {width, height} = this.props;
        let constraints = this.props.constraints ? this.props.constraints : {
            video: {
                facingMode: "environment",
                width,
                height
            }
        }

        try {
            let stream = await navigator.mediaDevices.getUserMedia(constraints);

            this.video.srcObject = stream;
            this.stream = stream;
            this.video.play();

        } catch (error) {

        }
    }

    render() {

        const {style, width, height} = this.props;
        console.log(style)
        return (
            <>
                <video id="video" style={style}/>
            </>
        );
    }
}

export default WebCamera;
