import React, {Component} from "react";
// import QrScanner from "./components/qr-scanner";
// import DisplayErrors from "./components/displayErrors";
import errorsMap from "./helpers/errorsMap";
import Errors from "./helpers/Errors";
import jsQR from "jsqr";
// import Webcam from "react-webcam";
import dataURLtoBlob from "blueimp-canvas-to-blob";
// import isJSON from "./helpers/isJSON";
import writeLog from "./helpers/writeLog";
import WebCamera from "./components/WebCamera";

const version = window.require("electron").remote.app.getVersion();

const {clipboard, shell} = window.require("electron");

class App extends Component {
    state = {
        step: 2,
        urlId: null,
        scanning: true,
        downloadURL: null,
        errors: [],
        orientationAngle: 0,
        canvasWidth: 442,
        canvasHeight: 642,
    };

    setAngle() {
        // default landscape-secondary
        let angle = 0;
        let width = 442;
        let height = 642;

        switch (window.screen.orientation.angle) {
            case 90: // portrait-primary
                angle = -90;
                width = 642;
                height = 442;
                break;
            case 270: // portrait-secondary
                angle = 90;
                width = 642;
                height = 442;
                break;
            case 180: // landscape-primary
                angle = -180;
                width = 442;
                height = 642;
                break;
        }

        this.setState({orientationAngle: angle, canvasWidth: width, canvasHeight: height})
    }

    rotateBase64Image(base64Image) {
        // create an off-screen canvas
        const offScreenCanvas = document.createElement('canvas');
        const offScreenCanvasCtx = offScreenCanvas.getContext('2d');
        /*  offScreenCanvas.width = 1024;
          offScreenCanvas.height = 1024;*/
        return new Promise((resolve) => {
            // create Image
            let img = new Image();
            img.src = base64Image;

            img.onload = () => {
                // set its dimension to rotated size

                console.log(`${img.width}x${img.height}`)
                offScreenCanvas.height = img.width;
                offScreenCanvas.width = img.height;

                /*switch (window.screen.orientation.type) {
                    case 'landscape-primary':
                        /!*      offScreenCanvasCtx.rotate(-90 * Math.PI / 180);
                              offScreenCanvasCtx.translate(-offScreenCanvas.height, 0);*!/
                        /!*
                        offScreenCanvasCtx.rotate(-90 * Math.PI / 180);
                        offScreenCanvasCtx.translate(-offScreenCanvas.height, 0);*!/
                        break;
                    case 'portrait-primary':
                        offScreenCanvasCtx.rotate(90 * Math.PI / 180);
                        offScreenCanvasCtx.translate(-offScreenCanvas.height, 0);
                        break;
                    case 'portrait-secondary':
                        offScreenCanvasCtx.rotate(90 * Math.PI / 180);
                        offScreenCanvasCtx.translate(0, -offScreenCanvas.width);
                        break;

                    default: // landscape-secondary
                        offScreenCanvasCtx.rotate(90 * Math.PI / 180);
                        offScreenCanvasCtx.translate(0, -offScreenCanvas.width);
                        break;
                }*/
                offScreenCanvasCtx.rotate(90 * Math.PI / 180);
                offScreenCanvasCtx.translate(0, -offScreenCanvas.width);
                console.log(window.screen.orientation.type)
                offScreenCanvasCtx.drawImage(img, 0, 0);

                // encode image to data-uri with base64
                resolve(offScreenCanvas.toDataURL("image/jpeg", 100));
            }
        });

    }

    screenOrientation() {

        this.setAngle();

        window.screen.orientation.addEventListener('change', () => {
            this.setAngle();
            console.log('new orientation is ', window.screen.orientation, this.state.orientationAngle);
        });
    }

    startVideo() {
        let video = document.getElementById("video");

        navigator.mediaDevices
            .getUserMedia({video: {facingMode: "environment"}})
            .then(stream => {
                video.srcObject = stream;
                video.play();
                requestAnimationFrame(this.tick.bind(this));
            })
            .catch(error => {
                let err = error;
                if (error.name == "TrackStartError" && error.message == "") {
                    err = new Errors(
                        "Camera is not available, please disable other processes that use the camera."
                    );
                }
                setTimeout(() => {
                    this.startVideo();
                }, 3000);
                this.errorHandler(err);
            });
    }

    componentDidMount() {
        // this.startVideo();
        this.screenOrientation();
    }

    sendFile = async () => {
        let endPoint = `http://reorder2-dev.telenorma.info:7777/image`;
        document.getElementById("loader").style.display = "block";
        try {
            this.setState({uploading: true});

            let rawImage = dataURLtoBlob(this.state.imageSrc);
            const formDataLocal = new FormData();
            formDataLocal.append("image", rawImage, `tmp.jpeg`);
            let responseRaw = await window.fetch(endPoint, {
                method: "POST",
                body: formDataLocal
            });
            let filename = await responseRaw.text();

            const downloadURL = `${endPoint}/${filename}`;
            const formData = new FormData();
            formData.set("data", JSON.stringify({url: downloadURL}));
            formData.set("token", this.state.token);
            let raw = await window.fetch(`${this.state.url}api/add_image/`, {
                method: "POST",
                body: formData
            });

            // let text = await raw.text();
            // console.log(text);
            // if(text.indexOf('{"result":"success"}') && !isJSON(text)) {
            //     this.setState({downloadURL, step: 4, uploading: false});
            //     return;
            // }

            let response = {};
            try {
                response = await raw.json();
                console.log(response);
            } catch (e) {
                throw new Errors("invalidResponse", "Server");
            }

            if (response.hasOwnProperty("error")) {
                throw new Errors(response.error, "Server");
            }

            this.setState({downloadURL, step: 4, uploading: false});
            // document.getElementById("loader").style.display = "none";
        } catch (error) {
            if (!error.hasOwnProperty("whoIsGuilty")) {
                error = new Errors(error.message, "Server");
            }

            this.setState({step: 1, uploading: false});
            this.startVideo();
            this.errorHandler(error);
        }
    };

    drawLine(begin, end, color) {
        let canvasElement = document.getElementById("canvas");
        let canvas = canvasElement.getContext("2d");
        canvas.beginPath();
        canvas.moveTo(begin.x, begin.y);
        canvas.lineTo(end.x, end.y);
        canvas.lineWidth = 4;
        canvas.strokeStyle = color;
        canvas.stroke();
    }

    tick() {
        try {
            //console.log(`tick`);
            let video = document.getElementById("video");
            let canvasElement = document.getElementById("canvas");
            let canvas = canvasElement.getContext("2d");
            let code = null;
            if (video && video.readyState === video.HAVE_ENOUGH_DATA) {
                canvasElement.hidden = false;
                canvasElement.height = video.videoHeight;
                canvasElement.width = video.videoWidth;

                // canvas.translate(canvasElement.width, 0);
                // canvas.scale(-1, 1);
                // canvas.rotate(0);

                canvas.drawImage(
                    video,
                    0,
                    0,
                    canvasElement.width,
                    canvasElement.height
                );
                let imageData = canvas.getImageData(
                    0,
                    0,
                    canvasElement.width,
                    canvasElement.height
                );
                code = jsQR(imageData.data, imageData.width, imageData.height, {
                    inversionAttempts: "dontInvert"
                });

                if (code) {
                    this.drawLine(
                        code.location.topLeftCorner,
                        code.location.topRightCorner,
                        "#e81d1d"
                    );
                    this.drawLine(
                        code.location.topRightCorner,
                        code.location.bottomRightCorner,
                        "#e81d1d"
                    );
                    this.drawLine(
                        code.location.bottomRightCorner,
                        code.location.bottomLeftCorner,
                        "#e81d1d"
                    );
                    this.drawLine(
                        code.location.bottomLeftCorner,
                        code.location.topLeftCorner,
                        "#e81d1d"
                    );

                    let parsedData = null;
                    try {
                        parsedData = JSON.parse(code.data);
                    } catch (e) {
                        throw new Errors("invalidQR");
                    }

                    //console.log(parsedData);
                    if (!parsedData.url) {
                        throw new Errors("invalidQR");
                    }
                    video.pause();
                    this.setState({
                        ...parsedData,
                        scanning: false,
                        step: 2
                    });
                }
            }
            if (!code) {
                requestAnimationFrame(this.tick.bind(this));
            }
        } catch (error) {
            requestAnimationFrame(this.tick.bind(this));
            this.errorHandler(error);
        }
    }

    setRef = webcam => {
        this.webcam = webcam;
    };

    capture = async () => {
        const _screenshot = this.webcam.takeFoto();
        const imageSrc = await this.rotateBase64Image(_screenshot);
        console.log(window.screen.orientation.type)
        this.setState({
            imageSrc,
            step: 3
        });
    };

    renderStep(step) {
    }

    clearErrors() {
        this.setState({errors: []});
    }

    errorHandler(error) {
        console.trace(`error:`, error, error.errorSide);
        console.trace(`error keys:`, Object.keys(error));
        let errors = this.state.errors;
        if (!errors.includes(errorsMap(error))) {
            errors.push(errorsMap(error));
            writeLog(error);
            this.setState({errors});
        }
    }

    render() {
        const showScanner = this.state.scanning;
        const {step} = this.state;
        const showWebCam = !this.state.scanning && !this.state.imageSrc;
        const showPreview = this.state.imageSrc && !this.state.downloadURL;
        const videoConstraints = {
            width: 1280,
            height: 720,
            // facingMode: "user"
        };

        return (
            <div className={`App ${step == 2 ? "" : ""}`}>
                <div className="App-header">
                    <div className="logo">
                        <img src={require("./assets/icons/android-chrome-192x192.png")}/>
                    </div>
                    <h2>Beleg-Scanner</h2>
                </div>

                {step == 1 && (
                    <>
                        <h4>Scannen von QR-Code</h4>
                        <canvas id="canvas" hidden style={{transform: `rotate(${this.state.orientationAngle}deg)`}}/>
                        <video id="video" hidden/>

                    </>
                )}
                <>
                    {step == 2 && window.screen.orientation.type != 'portrait-primary' && (
                        <div>
                            <img width={250} src={require("./assets/icons/rotate.png")} alt=""/>
                            <div>Bitte drehen Sie das Tablet um</div>
                        </div>
                    )}

                    {step == 2 && window.screen.orientation.type === 'portrait-primary' && (
                        <>
                            <div>
                                <h4>Bitte nehmen Sie ein Foto Ihres Belegs auf</h4>
                                {this.state.urlId && (
                                    <div id="output">
                                        <h4>URL:ID{this.state.urlId}</h4>
                                    </div>
                                )}
                                {/*<Webcam
                                    style={{transform: `rotate(${this.state.orientationAngle}deg)`}}
                                    videoConstraints={videoConstraints}
                                    width={this.state.canvasWidth}
                                    height={this.state.canvasHeight}
                                    className=""
                                    screenshotFormat="image/jpeg"
                                    ref={this.setRef}
                                    screenshotQuality={1}
                                />*/}
                                <WebCamera
                                    setRef={this.setRef}
                                    style={{transform: `rotate(${this.state.orientationAngle}deg)`}}
                                    width={this.state.canvasWidth}
                                    height={this.state.canvasHeight}
                                />
                                <div className="mt-3">
                                    <button
                                        className="btn btn-primary custom-btn"
                                        onClick={this.capture}
                                    >
                                        Photo aufnehmen
                                    </button>
                                </div>
                            </div>
                        </>
                    )}
                    {step == 3 && (
                        <>
                            <h4>Aufgenommener Beleg</h4>
                            <img
                                id="photo-taken"
                                src={`data:image/jpeg;base64${this.state.imageSrc}`}
                            />
                            <img id="loader" src={require("./assets/icons/loader2.svg")}/>
                            {!this.state.uploading && (
                                <div className="mt-3">
                                    <div
                                        className="btn btn-primary mr-2 custom-btn"
                                        onClick={this.sendFile}
                                    >
                                        Foto hochladen
                                    </div>
                                    <div
                                        className="btn btn-secondary mr-2 custom-btn"
                                        onClick={() => this.setState({imageSrc: null, step: 2})}
                                    >
                                        Anderes Foto aufnehmen
                                    </div>
                                    <div
                                        className="btn btn-secondary custom-btn"
                                        onClick={() => {
                                            this.setState({scanning: true, step: 1}, () => {
                                                this.startVideo();
                                            });
                                        }}
                                    >
                                        Neue QR-Code einscannen
                                    </div>
                                </div>
                            )}
                        </>
                    )}
                    {step == 4 && (
                        <div className="container py-3">
                            <p className="step4-thankyou">
                                Vielen Dank! Die Datei wurde hochgeladen. Bitte überprüfen Sie
                                den Link unten.
                            </p>
                            <div className="step4-block col-md-6">
                                <p>Link</p>
                                <input
                                    type="text"
                                    value={this.state.downloadURL}
                                    onChange={() => {
                                    }}
                                />
                                <div className="input-group-append">
                                    <button
                                        className="btn btn-primary custom-btn"
                                        type="button"
                                        onClick={() => clipboard.writeText(this.state.downloadURL)}
                                    >
                                        Kopieren
                                    </button>
                                    <button
                                        className="btn btn-primary custom-btn"
                                        type="button"
                                        onClick={event => {
                                            event.preventDefault();
                                            shell.openExternal(this.state.downloadURL);
                                        }}
                                    >
                                        Herunterladen
                                    </button>
                                </div>
                            </div>
                            <button
                                className="btn btn-primary custom-btn"
                                onClick={() => {
                                    this.setState({scanning: true, step: 1}, () => {
                                        this.startVideo();
                                    });
                                }}
                            >
                                Andere QR-Code einscannen
                            </button>
                        </div>
                    )}
                </>
                {/* {this.state.errors.length != 0 && (
          <DisplayErrors
            errors={this.state.errors}
            close={() => {
              this.clearErrors();
            }}
          />
        )} */}
                <div className="version pr-3">version: {version}</div>
            </div>
        );
    }
}

export default App;
